body {
  background-color: #a5b3b6;
}

.my-style {
  position: relative;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.btn-winter {
  background-color: #a5b3b6;
  position: relative;
  bottom: 16px;
}

.btn-owinter {
  background-color: #dad9d1;
}

.btn-winter:hover {
  background-color: #00394c;
  color: white;
}

.btn-owinter:hover {
  background-color: #00394c;
  color: white;
}

.nav-p {
  background-color: #dad9d1;
  position: fixed;
  z-index: 1;
  width: 100%;
  top: 0px;
  margin-bottom: 50px;
}

.nav-ph {
  background-color: #00394c;
  color: black;
}

.nav-ph1 {
  background-color: #00394c;
  color: white;
}

.nav-ph1:hover {
  background-color: #dad9d1;
  color: black;
}

.card-colr {
  background-color: #dad9d1;
}

.my-badge {
  width: 300px;
  position: relative;
  bottom: 252px;
}

.text-bg-owinter {
  background-color: #00394c;
}

.my-date {
  font-size: 15px;
  position: relative;
  top: 0px;
  margin: 0 !important;
}

.my-accord-bdy {
  background-color: #00394c;
  color: #dad9d1;
}

@media screen and (max-width: 768px) and (min-width: 260px) {
  .dark-mode {
    position: relative;
    right: 52px;
  }
  .mobile-comp {
    overflow-x: hidden;
    position: relative;
    right: 11px;
  }
}
